/* Float four columns side by side */
.column[data-v-245b813d] {
    float: left;
    width: 25%;
    padding: 0 10px;
    /* transition */
    -webkit-transition: height 4s;
    transition: height 4s;
}
.column[data-v-245b813d]:hover {
    height: 400px;
}

/* Remove extra left and right margins, due to padding */
.row[data-v-245b813d] {
    margin: 0 -5px;
    padding-top: 100px;
}

/* Clear floats after the columns */
.row[data-v-245b813d]:after {
  content: "";
  display: table;
  clear: both;
}

/* Responsive columns */
@media screen and (max-width: 600px) {
.column[data-v-245b813d] {
    width: 100%;
    display: block;
    margin-bottom: 20px;
}
}

